import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppComponent } from "../app.component";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";

@Component({
  selector: 'app-ensavoirplus',
  templateUrl: './ensavoirplus.component.html',
  styleUrls: ['./ensavoirplus.component.scss']
})
export class EnsavoirplusComponent extends AppComponent implements OnInit {

  public slidecnt: any;
  constructor(private titleService: Title,
  private metaTagService: Meta,
  activatedRoute: ActivatedRoute,
  appService: AppService,
  location: Location,
  spinner: NgxSpinnerService,
  router: Router,) {

    super(activatedRoute, router, appService, location, spinner);
    this.slidecnt = 4;
   }
  public Date: any;
  public min: any;
  public Ville: any;

  ngOnInit() {
    this.titleService.setTitle("Ohmytennis | Cours de tennis en ligne");
    this.metaTagService.removeTag('name=title');
    this.metaTagService.removeTag('name=keywords');
    this.metaTagService.removeTag('name=description');
    this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | Cours de tennis en ligne'});
    this.metaTagService.addTag({ name: 'keywords', content: 'Cours de tennis en ligne' });
    this.metaTagService.addTag({ name: 'keywords', content: 'tournoi de tennis' });
    this.metaTagService.addTag({ name: 'keywords', content: 'match tennis' });
    this.metaTagService.updateTag({ name: 'description', content: "Les services annexes proposs regroupent les cours de tennis en ligne (e-learning), les tournois de tennis amicaux ou officiels mais aussi de la billetterie pour assister  des matchs de tennis. Nhsitez pas  venir dcouvrir tout ce que nous vous proposons sur notre plateforme communautaire ddie au tennis." }, 'name="description"');
  }

}
