import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppComponent } from "../app.component";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";

@Component({
  selector: 'app-newsletters',
  templateUrl: './newsletters.component.html',
  styleUrls: ['./newsletters.component.scss']
})
export class NewslettersComponent extends AppComponent implements OnInit {
  public slidecnt: any;
  constructor(private titleService: Title,
  private metaTagService: Meta,
  activatedRoute: ActivatedRoute,
  appService: AppService,
  location: Location,
  spinner: NgxSpinnerService,
  router: Router,) {

    super(activatedRoute, router, appService, location, spinner);
    this.slidecnt = 4;
   }
  public Date: any;
  public min: any;
  public Ville: any;

  ngOnInit() {
     //SEO
     this.titleService.setTitle("Ohmytennis | Newsletter actualit tennis");
     this.metaTagService.removeTag('name=title');
     this.metaTagService.removeTag('name=keywords');
     this.metaTagService.removeTag('name=description');
     this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | Newsletter actualit tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'actualit tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'info tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'cours de tennis' });
     this.metaTagService.updateTag({ name: 'description', content: "La newsletter est une lettre dactualit sur le tennis vous permettant de maintenir un lien continue avec votre site internet. En effet tous les mois, vous recevrez dans votre boite email une slection de nos meilleurs services et contenus rdactionnels. Voici quelques thmes qui seront abords pendant lanne" }, 'name="description"');
  }

}