import { Component, OnInit } from '@angular/core';
import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { AppComponent } from "../app.component";
import { AppService } from "../shared/app.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Location } from "@angular/common";

@Component({
  selector: 'app-quisommenous',
  templateUrl: './quisommenous.component.html',
  styleUrls: ['./quisommenous.component.scss']
})
export class QuisommenousComponent  extends AppComponent implements OnInit {
  public slidecnt: any;
  constructor(private titleService: Title,
  private metaTagService: Meta,
  activatedRoute: ActivatedRoute,
  appService: AppService,
  location: Location,
  spinner: NgxSpinnerService,
  router: Router,) {

    super(activatedRoute, router, appService, location, spinner);
    this.slidecnt = 4;
   }
  public Date: any;
  public min: any;
  public Ville: any;

  ngOnInit() {
     //SEO
     this.titleService.setTitle("Ohmytennis | Prsentation des professeurs de tennis");
     this.metaTagService.removeTag('name=title');
     this.metaTagService.removeTag('name=keywords');
     this.metaTagService.removeTag('name=description');
     this.metaTagService.updateTag({ name: 'title', content: 'Ohmytennis | Prsentation des professeurs de tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'professeur de tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'cours tennis' });
     this.metaTagService.addTag({ name: 'keywords', content: 'leon individuelles tennis' });
     this.metaTagService.updateTag({ name: 'description', content: "Que propose notre site communautaire proposant la mise en relation entre les joueurs de tennis et les professionnels ? Nous mettons principalement des professeurs de tennis pour des cours avec tous les services annexes qui peuvent tre proposs. Du choix du prof en passant par la rservation mais aussi lachat de matriel" }, 'name="description"');
  }

}
